@import url(https://fonts.googleapis.com/css2?family=Iceland&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif; /*Iceland surely fits well*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
}

p {
font-family: 'Luckiest Guy', sans-serif;
}
h1,h2,h3{
font-family: 'Iceland', Arial;
}
.huge {
  font-size: 12rem;
  -webkit-background-clip: text;
          background-clip: text;
  background-image: url(https://upload.wikimedia.org/wikipedia/commons/6/69/NASA-HS201427a-HubbleUltraDeepField2014-20140603.jpg);
  background-size: cover;
  color: transparent;
}
